<template>

<div class="wrap">
  <div class="head">
        <div class="Avator">
          <img  v-if="doctor_list" @click.prevent="clickImg($event)" v-lazy="doctor_list.photoUrl">
        </div>
         <p  v-if="doctor_list" class="head_p1">{{doctor_list.name}}<span><i class="icon iconfont icon-auth"></i></span></p>

         <p  v-if="doctor_list" class="head_p2">{{doctor_list.degreeName}}</p>

         <router-link :to="{name:'hospital_appoint',params:{doctor_name:doctor_list.name,doctor_id:doctor_list.doctorId}}">
           <div class="halo">
            <div class="appoint_box">
                <i class="iconfont icon-yachi"></i>
                <p>预约看牙</p>
            </div> 
            <div class="pulse"></div>
            <div class="pulse_1"></div>
            <div class="pulse_2"></div>
          </div>
        </router-link>

  </div>

<big-img v-show="showImg" @clickit="viewImg"  :imgSrc="imgSrc"></big-img>

 <div class="content">
  <div class="part skills">
    <div>擅长</div>
    <div  v-if="doctor_list">{{doctor_list.goodAt}}</div>
  </div>

<div class="part">
   <div>简介</div>
   <div>
     <p  v-if="doctor_list" ref="brief" class="p_hidden">{{doctor_list.remark}} </p>
     <span @click="pack_up()" v-show="show_brief">收起<i class="iconfont icon-xiangshang_jiantou"></i></span>
     <span @click="show()" v-show="!show_brief">展开<i class="iconfont icon-jiantou9"></i></span>
    </div>
</div>


<div class="part_box">
 <div class="part_content">
    <div class="part">
        <div>证书</div>
        <div>
         <ul class="ui_list ui_list_1"> 
                  <li> 
                     <img  @click="clickImg($event)"  src="../../../static/img/rongyu.png">
                   </li>
                  <li> 
                     <img  @click="clickImg($event)"  src="../../../static/img/rongyu.png">
                  </li>
                  <li> 
                     <img  @click="clickImg($event)"  src="../../../static/img/rongyu.png">
                  </li>
        </ul>
        </div>
    </div>
    <div class="part">
        <div>风采</div>
        <div>
         <ul class="ui_list ui_list_2"> 
                  <li> 

                     <img  @click="clickImg($event)"  src="../../../static/img/media_raport1.jpg">
                   </li>
                  <li> 
                     <img  @click="clickImg($event)"  src="../../../static/img/media_raport2.jpg">
                 </li> 
        </ul>
        </div>
    </div>
 </div>
</div>

<div class="part_box">
 <div class="part_content">
  <div class="title border-bottom-solid">
   <p>
      接诊时间
   </p>
 </div>
 <p  v-if="doctor_list" class="time">
   {{doctor_list.visitTime}}
 </p>
 </div>
</div>

 <div class="part_box">
 <div class="part_content">
  <div class="title border-bottom-solid">
   <p>
      服务机构
   </p>
 </div>
 <div>
 <div></div>

<router-link to="/hospital"> 
    <div class="part_3 part_3_1  border-bottom-solid">
        <div class="left">
          <div class="img">
             <img src="../../../static/img/baya.png">
          </div>
        </div>
        <div class="center">
          <h3>汇亿康口腔</h3>
            <p><span class="position">逸景翠园</span><span>|</span>社区</p>
        </div>
        <div class="right">
           <i class="icon iconfont icon-youhua"></i>
        </div>
    </div>
</router-link>


<router-link to="/hospital"> 
   <div class="part_3 part_3_1  border-bottom-solid">
        <div class="left">
          <div class="img">
             <img src="../../../static/img/baya.png">
          </div>
        </div>
        <div class="center">
          <h3>汇亿康口腔</h3>
          <p><span class="position">信孚康乐</span><span>|</span>社区</p>
        </div>
        <div class="right">
          <i class="icon iconfont icon-youhua"></i>
        </div>
   </div>
 </router-link>

</div>
  </div>
 </div>


</div>

 </div>

</div>

</template>

<script type="text/ecmascript-6">
    import BigImg from "../base/big_img"
    import {Toast} from 'mint-ui'
    import {addClass} from '../../../static/js/dom.js'
    import {removeClass} from '../../../static/js/dom.js'
    export default {
    data(){
          return{
             showImg:false,
             doctor_title:null,
             doctor_skillful:null,
             imgSrc:'',
             show_brief:false,
             follow:false,
             doctor_list:'',
           }},
      watch: {},
      methods:{
        clickImg(e) {
            this.showing = true;
　　　　　　this.showImg = true;
　　　　　　// 获取当前图片地址
　　　　　　this.imgSrc = e.currentTarget.src;
            console.log(this.imgSrc )
　　　　},
　　　　viewImg(){
            this.showing = false;
　　　　　　this.showImg = false;
　　　　},
        pack_up(){
          addClass(this.$refs.brief,'p_hidden')
          this.show_brief=false
        },
        show(){
         removeClass(this.$refs.brief,'p_hidden')
          this.show_brief=true
        }},
       mounted(){},
      components: {
          BigImg
         },
      created(){
        console.log(this.$route.query.doctorId)
       //获取个人信息
        this.$http.get(this.GLOBAL.BASE_URL+'/huananhospital/findWcdoctorById?doctorId='+this.$route.query.doctorId).then((res)=>{
        console.log(res.data.data)
        this.doctor_list=res.data.data
     
      })
 

      }
}

</script>


<style lang="stylus" type="text/stylus" scoped>
 @keyframes scales
    0%   
        transform: scale(1)  
        opacity: 1
    
    25% 
        transform: scale(1.1) 
        opacity: 0.8
     
    50% 
        transform: scale(1.12)
        opacity: 0.5
    
    75% 
        transform: scale(1.14) 
        opacity: 0.5
  
    100% 
        transform: scale(0) 
        opacity: 0  
        
 @keyframes swinging
   0% 
     transform: rotate(0deg)
   25%
     transform: rotate(-10deg)
   50%
     transform: rotate(0deg)
   75%
     transform: rotate(10deg)
   100%
     transform: rotate(0deg)

 @keyframes spin
    0%   
        transform:scale(0.9) 
    
    50% 
        transform:rotate(1.1) 
     
    100% 
        transform:rotate(0.9)
    
 @keyframes halo 
    0%   
        transform: scale(0)  
        opacity: 0.0
    
    25% 
        transform: scale(0) 
        opacity: 0.1
     
    50% 
        transform: scale(0.1)
        opacity: 0.3
    
    75% 
        transform: scale(0.5) 
        opacity: 0.5
  
    100% 
        transform: scale(1) 
        opacity: 0.0  
.wrap
   background:#fff
  //background:#F2F2F2
.head
   padding:0.3rem 
   position:relative
   background:linear-gradient(to bottom,#E2E8FE,#fff)
   .halo
      width:2px
      height:2px
      position:fixed
      border-radius:50%
      top:4rem
      /*top:2.5rem*/
      right:0.9rem
      z-index:999
      border:1px solid red
      .scale-1px:after
        border-radius:50%
        border:1px solid red
      .appoint_box
          width:74px
          height:74px
          position:absolute
          border-radius:50%
          border-sizing:content-box
          background:#0075BF
          top:0
          bottom:0
          left:-36px
          margin:auto
          z-index:999
          color:#fff
          text-align:center
         .iconfont
           font-size:0.48rem
           display:block
           padding-top:10px
           transition:all 1s ease
           animation:spin 1.5s linear infinite
         p
           height:0.3rem
           line-height:0.3rem
           font-size:0.26rem
           animation:swinging  2s ease-in-out 3s 1 alternate both
      .pulse
          width:92px
          position: absolute
          top:0
          bottom:0
          left:-51px
          margin:auto
          border-sizing:content-box
          height:92px
          border-radius:50%
          border: 6px solid #0893EA
          opacity: 0 
          animation: halo 2s ease-out infinite 
      .pulse_1
          width:78px
          position: absolute
          top:0
          bottom:0
          border-sizing:content-box
          margin:auto
          left:-39px
          height:78px
          border-radius:50%
          opacity:1
          animation:scales 1.5s ease-out infinite
          border: 1px solid rgba(8,147,234.0.6)
      .pulse_2
          width:70px
          position: absolute
          border-sizing:content-box
          top:0
          bottom:0
          left:-35px
          margin:auto
          height:70px
          border-radius:50%
          animation:scales 2s ease-out infinite
          border:1px solid rgba(8,147,234.0.4)
   p
     text-align:center
   .head_p1
     height:0.8rem
     line-height:0.8rem
     font-size:0.32rem
     color:black
     .iconfont
       padding-left:0.1rem
       color:#38AEF4
   .head_p2
     /*padding:0 1.5rem*/
     font-size:0.28rem
   .Avator
           height:100%
           width:2.3rem
           height:2.3 rem
           border-radius:50%
           border:0.1rem solid #fff
           overflow:hidden
           margin:auto
           img
            width:100%
            height:100%
.doctor_header
       height:auto
       background:#0095D9
       padding:18px 0
       display:flex
       position:relative
       margin-bottom:15px
       div
         display:flex
         justify-content:center
         align-items:center
         .scale-1px:after
              border-radius:10px
              border:1px solid #fff
       div:nth-child(1)
         width:25%
         .Avator
           height:100%
           width:1.2rem
           height:1.2rem
           border-radius:50%
           overflow:hidden
           img
            width:100%
            height:100%
       div:nth-child(2)
          width:75%
          flex-direction:column
          p
            color:#fff
            text-align:left
            width:100%
            //border:1px solid red
          p:nth-child(1)
             font-size:0.29rem
             color:#F6E535
             height:0.4rem
             line-height:0.4rem
             span
               display:inline-block
               height:0.28rem
               line-height:0.28rem
               //border:1px solid red
               .iconfont 
                 color:#F4E336
                 //border:1px solid red
                 font-size:0.24rem
          p:nth-child(2)
            font-size:0.25rem
            padding-top:5px
            span
              span
                display:inline-block
                padding:0 3px
          p:nth-child(3)
            font-size:0.22rem
            span
              display:inline-block
              padding:0 3px
              z-index:2
              box-sizing:content-box
              margin-right:5px
              margin-top:5px
              border-radius:8px
            //box-shadow: 0px 2px  8px rgba(255,255,255,0.3)  
       div:nth-child(3)
         width:20%
         position:absolute
         top:0.33rem
         right:0.1rem
         span
            z-index:2
            display:inline-block
            padding:0px 4px
            color:#fff
            font-size:0.22rem
           .iconfont 
              color:#fff
              margin-right:2px 
              font-size:0.21rem 
           .icon-aixin
              color:red
.part
  display:flex
  padding-bottom:10px
  font-size:0.25rem
  .skills
    font-size:0.27rem
  div:nth-child(1)
    color:black
    width:18%
    font-size:0.27rem
    text-align:center
  div:nth-child(2)
    width:82%
    color:#555
    .skills
      color:#333
      display:inline-block
      padding-right:8px
    span
      color:#20A2DD
      .iconfont
        margin-left:5px
        font-size:0.24rem
    .ui_list_1
      li
        width:33.3%
        height:1.2rem
    .ui_list_2
      li
        width:33.3%
        height:1.3rem
    p
      padding-bottom:10px
  .part_content
    .part
      margin-top:10px
.time
      color:#999
      line-height:0.7rem
      height:0.7rem
      font-size:0.27rem
.part_3
        display:flex
        width:100%
        padding:5px 0 10px 0
        position:relative
        .left
         width:20%
         align-self:center 
         .img
           width:1.15rem
           height:1rem
           box-sizing:border-box
           padding:3px
           overflow:hidden
           img
             width:100%
             height:100%
         
        .center
           flex:1
           box-sizing:border-box
           padding-left:10px
           //padding:10px 5px 10px 15px 
           text-align:left
           //border:1px solid red
           h3
             font-size:0.26rem
             text-align:left
             height:0.4rem
             line-height:0.4rem
             color:#333
             font-weight:normal
           p
            color:#999
            font-size:0.26rem
            span
                display:inline-block
                margin-right:5px
                margin-bottom:5px
                padding:0 4px
                z-index:2
                font-size:0.22rem 
                color:#F58D34  
        .right
           position:absolute
           color:#999
           right:0
           top:0.25rem
.part_3_1
        .center
          p
            span
              color:#999
              font-size:0.26rem
              margin-right:4px
              margin-left:3px
              margin-bottom:5px
              padding:0 0px
              font-size:0.23rem
            .position
              color:red
              font-size:0.26rem
        .right
           position:absolute
           color:#999
           top:0.5rem
           right:10px
           .iconfont
             color:#ccc
             font-size:0.23rem    
    
   
   
</style>




